@charset "UTF-8";
@import "assets/variable.scss";
@import "assets/animation.scss";
body{
    background: #e3e3e3;
}
//navbar style
.navbar-default{
    padding:20px 0px;
    background-color: transparent;
    border-bottom: none;
    transition: .5s;
    .navbar-brand{
        color:white;
        &:hover{
            transition: .5s;
            color:$ored;
        }
    }
    .navbar-nav{
        perspective: 2000px;
        &>li{
            a{
                color: white;
                transition: .3s .3s;
            }
            &:hover>a{
                animation: navitem .5s forwards;
                color:$maincolor;
            }
        }
    }
}

//end of navbar style

//landing page style
#landing{
    background: linear-gradient(rgba(0,0,0,.5),rgba(0,0,0,.5)),url("../img/coffee-desk-laptop-notebook.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
//    min-height: 720px;
    .col-sm-12{
        padding:250px 15px;
        color: white;
        h1{
            font-size: 4em;
            font-weight: bold;
        }
        h3{
            font-size: 2.5em;
        }
        .btn{
            box-sizing: border-box;
            background: $maincolor;
            padding: 15px 30px;
            border: none;
            border-radius: 10px 0px 10px 0px;
            transition: .5s;
        }
    }
}
//end of the laddnign section style

//start of services section style

#services{
    padding:30px 0px;
    .text-section{
        padding: 100px 15px;
    }
    .img-section{
        .col-sm-6,.col-sm-12{
            padding-bottom: 15px;
        }
    }
}




//text color style
.text-ored{
    color:$ored;
}
.text-big{
    font-size: 50px;
    font-weight: bold;
}
