@keyframes navitem{
    0%{
        transform: rotateX(0deg);
        background: transparent;
    }
    50%{
        transform: rotateX(90deg);
    }
    100%{
        transform: rotateX(0deg);
        background: #fff;
    }
}
@keyframes textanimi{
    from{
        width: 0ch;
    }
    to{
        width: 32ch;
    }
}